<template>
    <div class="slider">
        <h3>{{ titulo }}</h3>
        <div class="container">
            <div class="row">
                <div class="col-sm-10">
                    <table class="slider-table" cellspacing="2px">
                        <tr>
                            <td v-for="(sesion, index) in slider.sesiones" 
                                :key="index" 
                                :colspan="sesion.numactividades" 
                                :class="classSesion(index)"
                                v-on:click="sesionSelec = index">
                            S-{{index+1}}
                            </td>
                        </tr>
                        <tr>
                            <td v-for="(actividad, index) in slider.actividades"
                                :key="index"
                                :class="classActividad(index)"
                                v-on:click="actividadSelec = index">
                                {{index + 1}}
                            </td>
                        </tr>
                    </table>
                </div>
                <div class="col-sm-2">
                    <table class="herramientas" cellspacing="2px">
                        <tr>
                        <th>
                                <button v-on:click="addSesion" class="btn btn-success btn-sm">+</button>
                                <button v-on:click="expandSesion" class="btn btn-primary btn-sm">&lt;&gt;</button>
                                <button v-on:click="reduceSesion" class="btn btn-primary btn-sm">&gt;&lt;</button>
                                <button v-on:click="deleteSesion" class="btn btn-danger btn-sm">-</button>
                            </th>
                        </tr>
                        <tr>
                            <td>
                                <button v-on:click="addActividad" class="btn btn-success btn-sm">+</button>
                                <button v-on:click="actividadToLeft" class="btn btn-primary btn-sm">&lt;&lt;</button>
                                <button v-on:click="actividadToRight" class="btn btn-primary btn-sm">&gt;&gt;</button>
                                <button v-on:click="deleteActividad" class="btn btn-danger btn-sm">-</button>
                            </td>
                        </tr>
                        
                    </table>
                </div>
            </div>
            <hr/>
            <div class="row">
                <label for="tituloactividad" class="col-sm-2 col-form-label">Título de la actividad</label>
                <div class="col-sm-6">
                    <input id="tituloactividad" type="text" v-model="slider.actividades[actividadSelec].titulo" class="form-control">
                </div>
                <div class="col-sm-4 from-group">
                    <label class="form-check-label">
                    <input id="tituloactividad" type="checkbox" v-model="slider.actividades[actividadSelec].evaluable" class="form-check-input">
                    Evaluable</label>
                </div>
            </div>
            <hr/>
            <div class="row">
                <div class="col-md-12">
                    <div class="from-group">
                        <label for="descripcionactividad">Descripción de la actividad</label>
                        <!-- <input id="tdescripcionactividad" type="text" v-model="slider.actividades[actividadSelec].descripcion" class="form-control"> -->
                        <ckeditor v-model="slider.actividades[actividadSelec].descripcion" :config="editorConfig"></ckeditor>
                    </div>
                </div>
            </div>
            <hr/>
            <div class="row">
                <div class="col-md-12">
                    <p>Recursos de la actividad</p >
                    <table class="table table-condensed">
                        <tr><th>título</th><th>tipo</th><th>URL</th><th><button v-on:click="addRecurso" class="btn btn-success btn-sm">+</button></th></tr>
                        <tr v-for="(unrecurso, index) in slider.actividades[actividadSelec].recursos" :key="index" :recursoinput="unrecurso" >
                            <td><input type="text" v-model="unrecurso.titulo" class="form-control"></td>
                            <td>
                            <select v-model="unrecurso.tipo" class="form-control">
                                <option value="rec">Recurso del profesorado</option>
                                <option value="cua">Cuaderno</option>
                                <option value="int">Interactiva</option>
                                <option value="ext">Externa</option>
                                <option value="eva">Evaluación</option>
                                <option value="vid">Vídeo</option>
                                <option value="mat">Material</option>
                            </select>
                            </td>
                            <td><input type="text" v-model="unrecurso.url" class="form-control"></td>
                            <td><button v-on:click="deleteRecurso(index)" class="btn btn-danger btn-sm">-</button></td>
                        </tr>
                    </table>
                </div>
            </div>
        </div>


    </div>
</template>

<script>


export default {
    name: 'Slider',

    props: {
        msg: String
    },

    data: function() {
        return {
            titulo: 'Crear slider',
            slider: {
                sesiones: [ { numactividades: 1}],
                actividades: [
                    { titulo: 'Actividad',
                    descripcion: '',
                    evaluable: false,
                    recursos: [
                            { titulo: 'recurso',
                            tipo: 'int',
                            url: ''
                            }
                        ]
                    }
                ]
            },
            sesionSelec: 0,
            actividadSelec: 0,
            editorConfig: {
                entities: false,
                toolbar: [
                    { name: 'document', items: [ 'Source']},
                    { name: 'clipboard', items: ['Undo', 'Redo']},
                    { name: 'basicstyles', items: [ 'Bold', 'Italic', 'Underline', 'Subscript', 'Superscript'] },
                    { name: 'paragraph', items: [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent' ] },
                    { name: 'styles', items: [ 'Format' ] },
                    { name: 'about', items: [ 'About' ] }
                ]
            }
        }
    },
    computed: {
        totalActividadesSesiones: function (){
            let total = 0;
            for (const sesion of this.slider.sesiones) {
                total = total + sesion.numactividades;
            }
            return total;
        }
    },
    methods: {
        classSesion(a) {
            return a == this.sesionSelec ? 'selected' : '' ;
        },
        classActividad(a) {
            return a == this.actividadSelec ? 'selected' : '' ;
        },
        ajustarActividadesEnSesiones() {
            this.slider.sesiones[this.slider.sesiones.length - 1].numactividades += this.slider.actividades.length - this.totalActividadesSesiones;
        },
        addSesion() {
            let nueva = { numactividades: 1};
            this.slider.sesiones.push(nueva);
            if (this.totalActividadesSesiones > this.slider.actividades.length){
                this.addActividad();
            } else {
                this.ajustarActividadesEnSesiones();
            }
            this.sesionSelec = this.slider.sesiones.length - 1;
        },
        deleteSesion() {
            if (this.slider.sesiones.length > 1){
                this.slider.sesiones.splice(this.sesionSelec, 1);
                this.ajustarActividadesEnSesiones();
                this.sesionSelec--;
            }
        },
        expandSesion() {
            if (this.sesionSelec == this.slider.sesiones.length-1) {
                this.slider.sesiones[this.sesionSelec].numactividades++;
                if (this.totalActividadesSesiones > this.slider.actividades.length){
                    this.addActividad();
                }
            } else if (this.slider.sesiones[this.sesionSelec + 1].numactividades > 1){
                this.slider.sesiones[this.sesionSelec].numactividades++;
                this.slider.sesiones[this.sesionSelec + 1].numactividades--;
            }

        },
        reduceSesion() {
           if (this.slider.sesiones[this.sesionSelec].numactividades > 1){            
                this.slider.sesiones[this.sesionSelec].numactividades--;
                this.slider.sesiones[this.sesionSelec + 1].numactividades++;
           }
        },
        addActividad(){
            let nueva = { titulo: '',
                        descripcion: '',
                        evaluable: false,
                        recursos: [ ] };
            this.slider.actividades.push(nueva);
            this.ajustarActividadesEnSesiones();
        },
        deleteActividad() {
            if (this.slider.actividades.length > 1) {
                if (confirm(`¿Está seguro de borrar la actividad ${this.actividadSelec + 1}?`)) {
                    this.slider.actividades.splice(this.actividadSelec, 1);
                    this.ajustarActividadesEnSesiones();
                    if (this.actividadSelec > 0) {
                        this.actividadSelec--;
                    }
                }
            }
        },
        actividadToLeft(){
            if (this.actividadSelec > 0) {
                let moverActividad = this.slider.actividades[this.actividadSelec];
                this.slider.actividades.splice(this.actividadSelec, 1);
                this.slider.actividades.splice(this.actividadSelec-1, 0, moverActividad);
                this.actividadSelec--;
            }
        },
        actividadToRight(){
            if (this.actividadSelec < this.slider.actividades.length - 1) {
                let moverActividad = this.slider.actividades[this.actividadSelec];
                this.slider.actividades.splice(this.actividadSelec, 1);
                this.slider.actividades.splice(this.actividadSelec+1, 0, moverActividad);
                this.actividadSelec++;
            }
         },
        addRecurso() {
            let nuevoRecurso = { titulo: "", tipo: "int", url: ""};
            this.slider.actividades[this.actividadSelec].recursos.push(nuevoRecurso);
        },
        deleteRecurso(key){
            if (confirm('¿Está seguro de borrar el recurso?')) {
                this.slider.actividades[this.actividadSelec].recursos.splice(key, 1);
            }
        }
    },
    mounted: function()  {
        this.titulo = this.msg
    },
    updated: function() {
        
        console.log(this.slider);

        const formcomponent = document.getElementById('sliderjson');
        formcomponent.innerHTML = JSON.stringify(this.slider);
    }
}
</script>

<style scoped>
.slider {
    width: 100%;
    margin: auto;
}
.slider-table {
    width: 100%;
    background-color: white;
}

.slider-table td {
    width: auto;
    background-color: gray;
    color: white;
    font-weight: bold;
    border: 2px white solid;
    padding: 3px;
}
th.selected {
    background-color: lightsalmon;
}
td.selected {
    background-color: lightsalmon;
}
.herramientas {
    background-color: whitesmoke;
    min-width: 160px;
}

</style>